import { ListenerManager } from "../../../../frame/scripts/Manager/ListenerManager";
import { SoundManager } from "../../../../frame/scripts/Manager/SoundManager";
import { Tools } from "../../../../frame/scripts/Utils/Tools";
import { UIHelp } from "../../../../frame/scripts/Utils/UIHelp";
import ButtonSync from "../../Components/ButtonSync";
import DragSync from "../../Components/DragSync";
import { EventType } from "../../Data/EventType";
import { SoundConfig } from "./SoundConfig";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GameItem extends cc.Component {

    @property(cc.Sprite)
    public sprite: cc.Sprite = null;
    @property(cc.Label)
    public label: cc.Label = null;
    @property(cc.Node)
    public huangKuang: cc.Node = null;
    @property(cc.Node)
    public hongKuang: cc.Node = null;
    @property(sp.Skeleton)
    public clickAin: sp.Skeleton = null;
    @property(cc.SpriteFrame)
    public spriteArr: cc.SpriteFrame[] = [];

    private _itemIndex: number = 0;
    private itemData: any = null;
    private rootNode = null;

    onLoad() {
        this.rootNode = cc.find('Canvas/GamePanel');
    }

    private getSpriteFrame(key) {
        let object = ["101", "102", "201", "202", "301", "302", "401", "402", "501", "502", "601", "602", "701", "702", "801", "802"];
        return this.spriteArr[object.indexOf(key)];
    }

    public setItemData(index: number, itemData: any) {
        this._itemIndex = index;
        this.itemData = itemData;

        this.label.string = itemData.wenben;
        this.sprite.spriteFrame = this.getSpriteFrame(itemData.kapaiId);

        this.node.name = index.toString();
        this.node.addComponent(ButtonSync).addTouchEvent(this.node, "GameItem", ["onClickStart", "onClickEnd"])
        this.node.getComponent(ButtonSync).setEnable(true);
    }





    private onClickStart() {

    }

    private onClickEnd() {
        if (this.huangKuang.active || this.hongKuang.active) return;
        this.clickAin.node.active = true;
        Tools.playSpine(this.clickAin, "animation", false, () => {
            this.clickAin.node.active = false;
        })
        ListenerManager.dispatch(EventType.CLICK_END, this._itemIndex);
    }

    public setHuangKuang(isShow) {
        this.huangKuang.active = isShow;
    }

    public setKuangNode(isRight: boolean, func: Function) {
        this.huangKuang.active = false;
        this.hongKuang.active = false;
        let node = isRight ? this.huangKuang : this.hongKuang;
        node.active = true;
        cc.tween(node).to(0, { opacity: 255 })
            .delay(0.2)
            .to(0, { opacity: 0 })
            .delay(0.2)
            .to(0, { opacity: 255 })
            .delay(0.2)
            .to(0, { opacity: 0 })
            .delay(0.2)
            .call(() => {
                node.active = false;
                if (isRight) {
                    cc.tween(this.node).to(0.2, { scale: 0 })
                        .call(() => {
                            UIHelp.closeMask();
                            func && func()
                            this.node.active = false;
                        })
                        .start();
                } else {
                    UIHelp.closeMask();
                    func && func()

                }
            })
            .start();
    }

}

